body {
  margin: 0;
  padding: 0;
  font-family: "Georgia", serif;
  background: radial-gradient(ellipse at top, #0f0f1a, #000000);
  color: #f8f8f8;
  overflow-x: hidden;
}

.App {
  text-align: center;
}

.landing {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: backgroundPulse 10s infinite alternate;
}

.title {
  font-size: 4rem;
  letter-spacing: 2px;
  margin-bottom: 1rem;
  text-shadow: 0 0 15px #6fd0ff;
}

.subtitle {
  font-size: 1.5rem;
  color: #aaa;
  margin-bottom: 2rem;
}

.enter-button {
  padding: 10px 24px;
  background-color: #222;
  color: #fff;
  border: 2px solid #6fd0ff;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.3s ease, transform 0.2s ease;
}

.enter-button:hover {
  background-color: #6fd0ff;
  color: #000;
  transform: scale(1.05);
}

.content-placeholder {
  padding: 4rem 2rem;
  background-color: #0a0a12;
  min-height: 100vh;
  border-top: 1px solid #222;
}

.fade-in {
  opacity: 0;
  animation: fadeIn 2s ease forwards;
}

.fade-in:nth-child(1) {
  animation-delay: 0.2s;
}
.fade-in:nth-child(2) {
  animation-delay: 1s;
}
.fade-in:nth-child(3) {
  animation-delay: 2s;
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

@keyframes backgroundPulse {
  from {
    background-color: #0f0f1a;
  }
  to {
    background-color: #12121c;
  }
}
